<template>
  <mrc_ess_myStep v-model="step"
                  :list='stepList'>
    <template v-slot:1>
      <base-info @success='success1'
                 :disabled='false'
                 :loading='loading'
                 :successText="$t('label_The_next_step')"
                 @cancel='cancel1'></base-info>
    </template>
    <template v-slot:2>
      <data-access-strategy @success='success2'
                            :disabled='false'
                            :loading='loading'
                            :cancelText="$t('label_The_previous_step')"
                            @cancel='cancel2'></data-access-strategy>
    </template>
  </mrc_ess_myStep>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import baseInfo from '../modules/baseInfo'
import dataAccessStrategy from '../modules/dataAccessStrategy'
export default {
  components: { baseInfo, dataAccessStrategy },
  props: {},
  data () {
    return {
      step: 2,

      params: {},
      loading: false
    };
  },
  validations: {},
  computed: {
    stepList () {
      return [
        { name: this.$t('label_Basic_information') },
        { name: this.$t('数据策略') },
      ]
    }
  },
  created () { },
  watch: {},
  mounted () { },

  methods: {
    // 下一步
    success1 () {
      this.step = 2
    },
    // 取消
    cancel1 () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_cancellation_of_the_edit') })
        .then(() => {
          this.$router.go(-1)
        }).catch(() => { })
    },
    // 提交
    success2 () {

    },
    // 上一步
    cancel2 () {
      this.step = 1
    }
  }
}
</script>

